/* ===========================================================================
  btn
============================================================================= */
.btn {
	display: block;
	width: 100%;
	margin-left: auto;
	margin-right: auto;
	padding: 20px;
	border: solid 1px $color_border-gray;
	color: $color_link;
	text-align: center;
	background-color: #fff;
	&:hover {
		color: $color_link-hover;
	}
	@include mq(s) {
		padding: 15px;
	}
}
.btn-m {
	@extend .btn;
	max-width: 550px;
	@include mq(s) {
		max-width: none;
	}
}
.btn-s {
	@extend .btn;
	max-width: 270px;
	@include mq(s) {
		max-width: none;
	}
}
.btn_text {
	padding: 0 20px;
	text-align: center;
	position: relative;
	@include arrow($color_link, 7px, 1px, right);
}
.btn_text-bottom {
	@extend .btn_text;
	&::after {
		transform: map-get($drct-arrow, bottom);
	}
}
.btn_text-back {
	@extend .btn_text;
	&::after {
		right: auto;
		left: 0;
		transform: map-get($drct-arrow, left);
	}
}
.btn_text-blank {
	@extend .btn_text;
	&::after {
		width: 14px;
		height: 10px;
		border: 0;
		background: resolve('icon_blank_gray.svg') no-repeat center/contain;
		transform: rotate(0);
	}
}
.btn_text-pdf {
	@extend .btn_text;
	padding-right: 35px;
	&::after {
		width: 28px;
		height: 15px;
		border: 0;
		background: resolve('icon_pdf.svg') no-repeat center/contain;
		transform: rotate(0);
	}
}